.vertical {
  writing-mode: bt-lr;
  height: 70px;
  appearance: slider-vertical;
}   
@keyframes fadeInOut {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes move {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(100px, 200px);
  }
}
@keyframes scale {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.5);
  }
}
@keyframes shape {
  0% {
    width: 50px;
    height: 50px;
    border-radius: 25px;
  }
  100% {
    width: 100px;
    height: 100px;
    border-radius: 0;
  }
}
@keyframes path {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(100px, 100px);
  }
  100% {
    transform: translate(200px, 0);
  }
}
@keyframes pulse {
  0% {
    transform: scale(1);
    transform: rotate(0deg);
  }
  50% {
    transform: scale(1.1);
    transform: rotate(15deg);
  }
  100% {
    transform: scale(1);
    
  }
}
@keyframes skew {
  0% {
    transform: skew(0deg, 0deg);
  }
  100% {
    transform: skew(45deg, 45deg);
  }
}
@keyframes wobble {
  0% {
    transform: rotate(0deg);
  }
  15% {
    transform: rotate(-5deg);
  }
  30% {
    transform: rotate(5deg);
  }
  45% {
    transform: rotate(-5deg);
  }
  60% {
    transform: rotate(5deg);
  }
  75% {
    transform: rotate(-5deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
@keyframes appear {
  0%{
    opacity: 0;
  }
  15% {
    transform: rotate(-5deg);
  }
  60% {
    transform: rotate(5deg);
  }
  100%{
    opacity: 1;
  }
  
}

